<script>
import {term, courses} from './store.js';

function print() {
    $courses.split(',').forEach((element) => 
    {
        fetch(`https://jcurda-api.herokuapp.com/?term=${$term}&course=${element}`)
            .then((data) => data.json())
        	.then((jsonData) => 
        	{
                console.log(jsonData);
        	});
        ;
    });
}

</script>

<button on:click={print}>click</button>

