import { writable } from "svelte/store";

export const theme = localStorage.getItem('theme') ? writable(localStorage.getItem('theme')) : writable('dark');
theme.subscribe(value => {
    localStorage.setItem("theme", value);
});

export const active = localStorage.getItem('active') ? writable(localStorage.getItem('active')) : writable('Select Term');
active.subscribe(value => {
    localStorage.setItem("active", value);
});

export const term = localStorage.getItem('term') ? writable(localStorage.getItem('term')) : writable('');
term.subscribe(value => {
    localStorage.setItem("term", value);
});

export const courses = localStorage.getItem('courses') ? writable(localStorage.getItem('courses')) : writable([]);
courses.subscribe(value => {
    localStorage.setItem("courses", value);
});
