<script>
    import {theme, active} from './store.js';
    const toggleTheme = () => {theme.set($theme == 'dark' ? 'light' : 'dark');}

    let tabs = ['Select Term', 'Select Courses', 'Create Schedule'];
	
	const toggleTab = (direction) => {
		if (direction == 'prev' && tabs.indexOf($active) > 0)
			active.set(tabs[tabs.indexOf($active) - 1]);
		else if (direction == 'next' && tabs.indexOf($active) + 1 < tabs.length)
            active.set(tabs[tabs.indexOf($active) + 1]);
	}
</script>




<header class={$theme}>
    {#if $theme == 'dark'}
        <button on:click={toggleTheme} class='toggle'>Light<br>Mode</button>
    {:else}
        <button on:click={toggleTheme} class='toggle'>Dark<br>Mode</button>
    {/if}
    <!-- <p>Instead of rendering all your data, &lt;VirtualList&gt; just renders the bits that are visible, keeping your page nice and light.</p>
    <p>The source code for the component is <a href='https://github.com/sveltejs/svelte-virtual-list'>here</a>.</p> -->

    <button class='left' on:click={() => toggleTab('prev')}>prev</button>
    <h1>{$active}</h1>
    <button class='right' on:click={() => toggleTab('next')}>next</button> <br>
</header>

<style>
    header {
        height: 95px;
        margin-bottom: 2em;
        /* border-bottom: 1px solid var(--gray3); */
    }

    h1 {
		/* display: inline; */
		text-transform: uppercase;
		font-size: 4em;
		font-weight: 100;
        margin-top: 0;
        top: 100px;
        margin-bottom: 10px;
    }

    .light h1 {
		color: rgb(185, 0, 185);
	}

	.dark h1 {
		color: rgb(139, 160, 255);
    }
    
    button {
        opacity: 0.7;
        transition: opacity 0.5s, visibility 0.5s;
		border-radius: 50%;
		background-color: #000;
		color: white;
        position: absolute;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        cursor: pointer;
        top: 1em;
        bottom: auto;
        z-index: 100;
        width: 4em;
        height: 4em;
        border: none;
    }
    
    .toggle {
        right: 2em;
    }

    .left {
        left: 15%;
    }

    .right {
        right: 15%;
    }

</style>